<div class="register"><h2 class="mb-3">Register</h2><form class="row" @submit.prevent="onSubmit"><div class="col-md-6 col-lg-4 col-xl-3 mx-auto"><div class="mb-3"><input class="form-control" :class="{'is-invalid': !!error.validation.name}" v-model="name" type="text" name="name" placeholder="Name" required><div class="invalid-feedback text-start" v-if="error.validation.name"><template v-for="message in error.validation.name">{{ message }}<br></template></div></div><div class="mb-3"><input class="form-control" :class="{'is-invalid': !!error.validation.email}" v-model="email" type="email" name="email" placeholder="Email" required><div class="invalid-feedback text-start" v-if="error.validation.email"><template v-for="message in error.validation.email">{{ message }}<br></template></div></div><div class="mb-3"><input class="form-control" :class="{'is-invalid': !!error.validation.password}" v-model="password" type="password" name="password" placeholder="Password" autocomplete="off" required><div class="invalid-feedback text-start" v-if="error.validation.password"><template v-for="message in error.validation.password">{{ message }}<br></template></div></div><div class="mb-3"><input class="form-control" v-model="passwordConfirmation" type="password" name="password_confirmation" placeholder="Password Confirmation" autocomplete="off" required></div><button class="btn btn-primary" :disabled="loading._" type="submit">Submit</button></div></form></div>